.tall {
  min-height: 70vh;
}

.margined {
  margin: 1em;
}

.spaced {
  margin-right: 1em;
}

.trash {
  position: absolute;
  padding: 5px;
  margin-top: 0.3em;
  margin-left: 0.6em;
}

.grey {
  color: grey;
}

.unpadded {
  padding: 5px;
  margin-right: 0.3em;
}

:invalid {
  border: tomato solid 3px;
}

.outlined {
  border: solid 2px;
  border-radius: 10em;
  margin-left: 0;
  padding-right: 0.3em;
}

.right {
  text-align: right;
}

.search {
  margin-right: 1em;
}
